@import "normalize.css";
@import "components/about-work.css";
@import "components/design-cost.css";

@font-face{
  font-family: 'Alegreya Sans';
  src: url(../fonts/AlegreyaSans-Regular.ttf) format("truetype");
  font-weight: normal;
  font-style: normal;
}

@font-face{
  font-family: 'Alegreya Sans';
  src: url(../fonts/AlegreyaSans-Bold.ttf) format("truetype");
  font-weight: bold;
  font-style: normal;
}

*,
*:before,
*:after {
  box-sizing: border-box;
}

body {
  margin: 0;
  color: #6a696b;
  font-family: 'Alegreya Sans', sans-serif;
}

img {
  display: block;
  max-width: 100%;
  max-height: 100%;
}

a {
  text-decoration: none;
}

ul {
  list-style: none;
  padding: 0;
}

p {
  margin: 0;
}

h1 {
  display: none;
}

h2 {
  font-size: 39px;
  font-weight: 600;
}

h3 {
  font-size: 21px;
  font-weight: 600;
}

h1, h2, h3, h4, h5, h6 {
  color: #000000;
  margin: 0;
}